body {
  background: url(https://i.imgur.com/L6CzgSG.jpg) no-repeat center center fixed;
}

h1 {
  margin: .7rem 0rem .7rem 0rem
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 20vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.card {
  width: 320px;
  height: 500px;
  background-color: #4c69ba;
  border-radius: 10px;
  margin: 20px auto;
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.1), 2px 2px 8px rgba(0, 0, 0, 0.1);
}

.card .card-header {
  height: 220px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  /* background: url(https://i.imgur.com/muzcDX7.jpg);
  background-size: cover; */
}

.card .card-body {
  position: relative;
  color: white
}

.card .card-body img {
  height: 100px;
  width: 100px;
  border-radius: 10px;
  border: 3px solid rgb(255, 255, 255);
  position: absolute;
  top: -50px;
  left: 50%;
  margin-left: -50px;
  box-shadow: 0 6px 6px -4px rgba(0, 0, 0, 0.2);
}

.card .card-body .name {
  font-size: 32px;
  text-align: center;
  padding-top: 60px;
  margin-bottom: 10px;
}

.card .card-body .description {
  padding: 0 25px;
  line-height: 1.5;
  text-align: center;
  margin-bottom: 25px;
}

.card .card-body .location {
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-align: center;
}